<template>
    <div class="container">

        <div class="note">
            <div class="title">公告</div>
            <div class="notice-content">
                把脑袋里的东西好好捋一捋
            </div>
        </div>
        <div class="list">
            <div class="title">分类</div>
            <ul class="ullist">
                <li class="ulitem" v-for="(tag) in shownTags" :key="tag" @click="navToCategory(tag)"><span
                        class="item-text">{{ tag }}</span><span class="number"> ({{
                            tags[tag]
                        }})</span>
                </li>
            </ul>
        </div>

        <div class="list">
            <div class="title">归档</div>
            <ul class="ullist">
                <li class="ulitem" v-for="(year) in shownBlogYears" :key="year" @click="navToArchive(year)"><span
                        class="item-text">{{ year
                        }}</span><span class="number">
                        ({{ blogYears[year] }})</span></li>
            </ul>
        </div>

        <div class="list">
            <div class="title">最新文章</div>
            <ul class="ullist">
                <li class="ulitem" v-for="(blog) in shownBlogs" :key="blog.fileName" @click="navToDetail(blog)"><span
                        class="item-text">{{ blog.title
                        }}</span></li>
            </ul>
        </div>
    </div>
</template>
<script>

import { useBlogStore } from '../store/blogstore.js'
import { storeToRefs } from 'pinia'

export default {
    data() {
        return {
            blogs: [],
            tags: {},
            blogYears: {},
        }
    },

    computed: {
        shownBlogs() {
            if (this.blogs.length > 5) {
                return this.blogs.slice(0, 5)
            } else {
                return this.blogs
            }
        },
        //检查到tags更新时同步更新shownTags
        shownTags() {
            const sortedTags = Object.keys(this.tags).sort((a, b) => this.tags[b] - this.tags[a]);
            if (sortedTags.length > 5) {
                return sortedTags.slice(0, 5)
            } else {
                return sortedTags
            }
        },
        shownBlogYears() {
            const shownBlogYears = []
            Object.keys(this.blogYears).sort((a, b) => b - a).forEach((year) => {
                shownBlogYears.push(year)
            })
            return shownBlogYears
        }
    },

    mounted() {
        const blogStore = useBlogStore()
        const { blogList, tags, years } = storeToRefs(blogStore)
        this.blogs = blogList
        this.tags = tags
        this.blogYears = years
    },

    methods: {
        navToArchive(year) {
            this.$router.push({
                path: '/archive',
                query: {
                    year: year
                }
            })
        },
        navToCategory(tag) {
            this.$router.push({
                path: '/category',
                query: {
                    tag: tag
                }
            })
        },
        navToDetail(blog) {
            this.$router.push({
                path: '/detail',
                //传递blog参数到detail页面
                query: {
                    fileName: blog.fileName
                }
            })
        },
    }
}

</script>
