//导入createRouter方法
import { createRouter, createWebHashHistory } from "vue-router";

const routes = [
  {
    path: "/",
    // 重定向到home
    redirect: "/home",
  },

  // home
  {
    path: "/home",
    name: "home",
    component: () => {
      return import("../views/HomePage.vue");
    },
    meta: {
      keepAlive: true,
    },
  },

  {
    path: "/archive",
    component: () => {
      return import("../views/ArchivePage.vue");
    },
  },

  {
    path: "/category",
    component: () => {
      return import("../views/CategoryPage.vue");
    },
  },

  {
    path: "/detail",
    name: "detail",
    component: () => {
      return import("../views/DetailPage.vue");
    },
    meta: {
      keepAlive: true,
    },
  },

  {
    path: "/search",
    name: "search",
    component: () => {
      return import("../views/SearchPage.vue");
    },
    meta: {
      keepAlive: true,
    },
  },

  // about
  {
    path: "/about",
    component: () => {
      return import("../views/AboutPage.vue");
    },
  },
];

// 3. 创建路由实例
const router = createRouter({
  history: createWebHashHistory(),
  routes,
  scrollBehavior(to, from, savedPosition) {
    // return 期望滚动到哪个的位置
    if (savedPosition) {
      return savedPosition;
    }
    if (from.meta.keepAlive) {
      from.meta.savedPosition = document.body.scrollTop;
    }
    return {
      x: 0,
      y: to.meta.savedPosition || 0,
    };
  },
});

// 4. 导出路由实例
export default router;
