import { defineStore } from "pinia";

/**
 * pinia 是一个状态管理库，它的状态是保存在内存中的，而不会被自动地持久化到浏览器的本地存储。因此，当你刷新页面时，状态会被重置为空。
  要在刷新页面后保留状态，你需要将状态持久化到浏览器的本地存储中，然后在页面加载时从本地存储中恢复状态。
  你可以使用浏览器提供的 localStorage 或 sessionStorage 来实现这一点。
 */
const initialBlogs = JSON.parse(localStorage.getItem("blogList")) || [];
const initialTags = JSON.parse(localStorage.getItem("blogTags")) || {};
const initialYears = JSON.parse(localStorage.getItem("blogYears")) || {};

export const useBlogStore = defineStore("blog", {
  state: () => ({
    blogList: initialBlogs,
    tags: initialTags,
    years: initialYears,
  }),
  getters: {
    getBlogList() {
      return this.blogList;
    },
    getTags() {
      return this.tags;
    },
    getYears() {
      return this.years;
    },
  },
  actions: {
    //加载blogs
    loadBlogs() {
      fetch("/blogs/index.json")
        .then((response) => response.json())
        .then((json) => {
          const blogs = json["blogs"];
          const updateTime = json["updateTime"];
          const preUpdateTime = localStorage.getItem("updateTime");
          console.log("loadBlogs", blogs, updateTime, preUpdateTime);
          if (this.blogList.length == 0 || updateTime != preUpdateTime) {
            //如果当前blogList为空，或者更新时间不一致，更新blogList
            console.log("updateBlogList", this.blogList.length == 0, updateTime != preUpdateTime)
            this.updateBlogList(blogs, updateTime);
          }
          // if (this.blogs.length > 0) {
          //   console.log("set blog", this.blogs);
          //   blogStore.setBlogList(this.blogs);
          // }
        })
        .catch((err) => {
          console.log("error", err);
        });
    },

    updateBlogList(blogs, updateTime) {
      console.log("updateBlogList", blogs);
      //将blogs按照时间字符串排序
      const sortedBlogList = blogs.sort((a, b) => {
        return this.compareDateStr(b["createDate"], a["createDate"]);
      });
      //获取blog标签
      const blogTags = blogs.reduce((tags, blog) => {
        //记录标签出现的次数
        if (blog.tags) {
          blog.tags.split(",").forEach((tag) => {
            if (tags[tag]) {
              tags[tag] += 1;
            } else {
              tags[tag] = 1;
            }
          });
        }
        return tags;
      }, {});

      const blogYears = blogs.reduce((years, blog) => {
        if (blog.createDate) {
          const year = blog.createDate.split("-")[0];
          if (years[year]) {
            years[year] += 1;
          } else {
            years[year] = 1;
          }
        }
        return years;
      }, {});
      this.blogList = sortedBlogList;
      this.tags = blogTags;
      this.years = blogYears;
      console.log("updateBlogList", this.blogList, this.tags, this.years);
      localStorage.setItem("blogList", JSON.stringify(sortedBlogList));
      localStorage.setItem("blogTags", JSON.stringify(blogTags));
      localStorage.setItem("blogYears", JSON.stringify(blogYears));
      localStorage.setItem("updateTime", updateTime);
    },

    getBlogByFileName(fileName) {
      console.log("getBlogByFileName", fileName);
      return this.blogList.find((blog) => blog.fileName === fileName);
    },

    //添加两个日期字符串的比较方法
    compareDateStr(dateStr1, dateStr2) {
      const date1 = new Date(dateStr1);
      const date2 = new Date(dateStr2);
      return date1 - date2;
    },
  },
});
